/* Reset básico */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Corpo da página */
body {
  font-family: Arial, sans-serif;
  background-color: #f9f9f9;
  color: #333;
  line-height: 1.6;
}

/* Estilo para navegação */
nav {
  background-color: #4CAF50;
  padding: 1rem;
  text-align: center;
}

nav a {
  color: white;
  margin: 0 10px;
  text-decoration: none;
  font-weight: bold;
}

nav a:hover {
  text-decoration: underline;
}

/* Container principal das páginas */
.container {
  max-width: 1200px;
  margin: 20px auto;
  padding: 20px;
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Títulos das páginas */
h1 {
  color: #4CAF50;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

/* Estilos para os parágrafos */
p {
  font-size: 1.2rem;
  color: #555;
}
